.contact-form-section {
  background: #fff;
  padding: 100px 80px;
  text-align: center;
}
.form-tag {
  display: inline-block;
  border: 1px solid #e5e5e5;
  padding: 10px 15px;
  border-radius: 25px;
  color: #12175c;
  font-size: 14px;
  font-weight: 600;
}
.form-heading {
  font-size: 65px;
  font-weight: 600;
  line-height: 1.1em;
  color: #12175c;
  margin: 0 0 50px 0;
}
.contact-form {
  max-width: 800px;
  margin: 0 auto;
  background: #fff;
  border-radius: 15px;
  padding: 50px 40px;
  box-shadow: 0 4px 25px rgba(0, 0, 0, 0.1);
  text-align: left;
}
.form-row {
  display: flex;
  flex-wrap: flex;
  gap: 50px;
}
.form-group {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-bottom: 25px;
}
.form-group input,
.form-group textarea
{
  border: none;
  border-bottom: 1px solid #d9d9d9;
  padding: 10px 5px;
}
