.contact-info-section {
  background: #f9f9f9;
  padding: 80px 20px;
  display: flex;
  justify-content: center;
}
.contact-info-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 150px;
  max-width: 1100px;
  text-align: center;
}
.contact-info-box {
  display: flex;
  flex-direction: column;
  align-items: start;
  /* flex: 1 1 250px; */
}
.icon-box {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 70px;
  height: 70px;
  background: #ededf1;
  border-radius: 50%;
  margin-bottom: 10px;
}
.contact-info-box h4 {
  font-family: "instrument-sans";
  letter-spacing: 1.1px;
  font-size: 16px;
  color: #12175c;
  margin-bottom: 10px;
  font-weight: 400;
}
.number-link,
.email-link,
.hours-link {
  font-family: "plus-jakarta-sans";
  font-size: 18px;
  color: #12175c;
  text-decoration: underline;
  font-weight: 550;
  text-underline-offset: 2px;
  transition: color 0.3s ease;
}
.hours-link {
  margin: 0px;
}
.number-link:hover {
  color: #4a8be5;
}
.hours-link:hover {
  color: #4a8be5;
}
.email-link:hover {
  color: #4a8be5;
}
